@font-face {
    font-family: swiper-icons;
    src: url('data:application/font-woff;charset=utf-8;base64, ');
    font-weight: 400;
    font-style: normal;
  }
  
  :root {
    --swiper-theme-color: #007aff;
  }
  
  .swiper {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    padding: 0;
    z-index: 1;
  }
  
  .swiper-vertical > .swiper-wrapper {
