  }
  
  .swiper-creative .swiper-slide {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    overflow: hidden;
    transition-property: transform, opacity, height;
  }
  
  .swiper-cards {
    overflow: visible;
  }
  
  .swiper-cards .swiper-slide {
    transform-origin: center bottom;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    overflow: hidden;
  }
  
  .swiper {
    background-color: transparent;
  
    @media screen and (max-width: 767px) {
      max-width: 300px;
    }
  
    @media screen and (min-width: 768px) {
      width: 100%;
    }
  }
  
  .swiper-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin-bottom: 14px;
  
    @media screen and (min-width: 768px) {
      margin-bottom: 32px;
    }
  }
  
  .swiper-list__item {
    /* Center slide text vertically */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
  }
  
  .swiper-list__image-container {
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 32px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .swiper-list__reviews-container {
    border-radius: 10px;
    padding: 40px 40px 34px;
    margin-bottom: 30px;
    text-align: center;
  
    @media screen and (min-width: 768px) {
      padding: 30px 80px;
      border-radius: 24px;
      margin-bottom: 32px;
    }
  
    @media screen and (min-width: 768px) {
      padding: 44px 109px;
      border-radius: 24px;
    }
  }
  
  .swiper-list__reviews-text {
    font-size: 12px;
    line-height: 2.55;
    text-align: center;
  
    color: var(--primary-lighttext-color);
  
    @media screen and (min-width: 768px) {
      font-weight: 500;
      font-size: 14px;
      line-height: 1.86;
      letter-spacing: 0.04em;
    }
  
    @media screen and (min-width: 1200px) {
      font-size: 16px;
      letter-spacing: 0.04em;
    }
  }
  
  .swiper-list__reviews-text--block {
    @media screen and (max-width: 767px) {
      display: block;
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      color: #000000;
    }
  }
  
  .swiper-list__customer-meta {
    font-weight: 500;
    font-size: 20px;
    line-height: 186%;
    text-align: center;
  
    color: var(--primary-darktext-color);
  }
  
  //Pagination
  .swiper-pagination {
    position: static; //Because default pagination styles have position: absolute
  }
  
  .swiper-pagination-bullet {
    .swiper-pagination & {
      width: 12px;
      height: 12px;
      background-color: var(--primary-background-color);
      margin-right: 18px;
  
      &:last-child {
        margin-right: 0;
      }
    }
  }
  
  .gallery {
    @media screen and (max-width: 767px) {
      background-image: linear-gradient(
        var(--primary-background-color) 25%,
        var(--primary-lighttext-color) 25% 100%
      );
    }
  
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      background-image: linear-gradient(
        var(--primary-background-color) 50%,
        var(--primary-lighttext-color) 50% 100%
      );
    }
  
    @media screen and (min-width: 1200px) {
      background-image: linear-gradient(
        var(--primary-background-color) 20%,
        rgba(255, 255, 255, 0) 20% 100%
      );
    }
  }
  
  .gallery-wrapper {
    display: flex;
    justify-content: center;
  
    @media screen and (max-width: 767px) {
      border-radius: 8px;
    }
  
    @media screen and (min-width: 768px) {
      border-radius: 18px;
    }
  
    @media screen and (min-width: 1200px) {
      border-radius: 24px;
    }
  }
  